            this.contador++;

            if (this.jugador1 == true) {
              const element = document.getElementById("icon" + id + "");
              element.innerHTML = "O";
              this.jugador1 = false;
              this.jugador2 = true;
            } else {
              const element = document.getElementById("icon" + id + "");
              element.innerHTML = "X";
              this.jugador1 = true;
              this.jugador2 = false;
            }

